/* Add a media query to move images below text when the device width is less than 1000px */
@media (max-width: 1000px) {
    .main {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  
    .images {
      margin-top: 20px;
    }
  
    .images div {
      width: 100%; /* Make each image take up the full width */
      margin-bottom: 20px;
    }
  }
  
  .main {
    display: flex;
    justify-content: space-between;
    align-items: center; /* Align items vertically at the center */
    padding: 20px; /* Add padding as needed */
  }
  
  .text {
    margin-left: 50px;
  }
  
  .images {
    display: flex; /* Use flexbox for the images container */
    align-items: center; /* Align items vertically at the center */
  }
  
  .images div {
    overflow: hidden; /* Hide overflow for smooth transition */
    transition: all 0.3s ease; /* Add a smooth transition effect */
  }
  
  .images img {
    width: 100%; /* Make sure the image fills the container */
    height: auto; /* Maintain the aspect ratio */
  }
  
  .image-container {
    width: 150px; /* Set a fixed width for rectangular shape */
    height: 100px; /* Set a fixed height for rectangular shape */
    margin-right: 10px; /* Add margin between images as needed */
  }
  
  .images div:first-child {
    background-color: #5384FB; /* Add background color to the first image */
    border-radius: 20px;
    height: 500px;
    width: 300px;
  }
  
  .images div:nth-child(2) {
    background-color: #5DB9FD; /* Add background color to the second image */
    border-radius: 20px;
    height: 500px;
    padding: 30px;
    margin-left: 20px; /* Add margin between the first and second images */
  }
  
  .images div:last-child {
    background-color: #5CB2C5; /* Add background color to the third image */
    height: 500px;
    border-radius: 20px;
  }
  
  .images div:hover {
    transform: scale(1.2); /* Enlarge the images on hover */
  }
  